.checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
  user-select: none;
}

.checkbox__input {
  position: absolute;
  top: 0;
  height: 0;
  width: 0;
  opacity: 0;
  visibility: hidden;
}

.checkbox__input:checked ~ .checkbox__icon:before {
  transform: scale(1) rotate(45deg) translate(-1px, -1px);
}

.checkbox__text {
  display: inline-block;
  max-width: 390px;
  padding-left: 32px;
  text-shadow: 0 3px 11px #000000;
  color: #fff;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.2;
  position: relative;
  text-align: center;
}

@media only screen and (max-width: 1023px) {
  .checkbox__text {
    max-width: 350px;
    padding-left: 24px;
    font-size: 21px;
  }
}

@media only screen and (max-width: 599px) {
  .checkbox__text {
    font-size: 12px;
    padding-left: 6px;
    text-align: left;
  }
}

.checkbox__text a {
  color: inherit;
  text-decoration: none;
  transition: color 0.3s;
}

.no-touch .checkbox__text a:hover {
  color: #14b737;
}

.checkbox__icon {
  display: inline-block;
  flex-shrink: 0;
  position: relative;
  width: 44px;
  height: 44px;
  border-radius: 3px;
  border: 3px solid #cfcfcf;
  background-color: #cfcfcf;
}

@media only screen and (max-width: 1023px) {
  .checkbox__icon {
    height: 40px;
    width: 40px;
  }
}

@media only screen and (max-width: 599px) {
  .checkbox__icon {
    width: 20px;
    height: 20px;
  }
}

.has-error .checkbox__icon {
  border-color: #ff0000;
}

.checkbox__icon:before {
  content: '';
  position: absolute;
  transition: transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
  transform: scale(0) rotate(45deg) translate(-1px, -1px);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 1;
  border-right: 2px solid #14b737;
  border-bottom: 2px solid #14b737;
  height: 15px;
  width: 12px;
}

@media only screen and (max-width: 599px) {
  .checkbox__icon:before {
    height: 10px;
    width: 8px;
  }
}

[data-modal] {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  min-width: 320px;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translate3d(0, 0, 0);
  overflow: auto;
  transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
  background-color: rgba(0, 0, 0, 0.5);
}

[data-modal].is-open {
  opacity: 1;
  visibility: visible;
  z-index: 100;
}

[data-modal-container] {
  position: relative;
  z-index: 1;
}

